.Programs {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 0 2rem 0 2rem;
}
.programs-header {
  display: flex;
  gap: 5rem;
  font-weight: bold;
  font-size: 3rem;
  justify-content: center;
  color: white;
  text-transform: uppercase;
  font-style: italic;
}
.program-categories {
  display: flex;
  gap: 1rem;
}
.category {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  gap: 1rem;
  color: white;
  justify-content: space-between;
  align-items: center;
}
.join-now {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.join-now > img {
  width: 1rem;
}
@media screen and (max-width: 768px) {
  .programs-header {
    flex-direction: column;
    gap: 1rem;
    font-size: x-large;
    align-items: center;
    justify-content: center;
    margin-top: 2rem;
  }
  .program-categories {
    flex-direction: column;
  }
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-category {
  text-align: center;
  font-size: 18px;
  background-color: gray;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-category:hover {
  background: var(--planCard);
  cursor: pointer;
}

.swiper-category img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.category > :nth-child(1) {
  width: 4rem;
  height: 4rem;
}
.category > :nth-child(2) {
  font-size: 1.5rem;
  font-weight: bold;
  text-transform: capitalize;
}
.category > :nth-child(3) {
  font-size: 0.9rem;
  line-height: 25px;
}

.form {
  margin: 0 auto 2rem;
  width: 70%;
}

.search-form {
  display: flex;
}

.input-search {
  flex: 1;
  font-size: 1rem;
  font-weight: 600;
  height: 3rem;
  border-radius: 10px 0 0 10px;
  border: 3px solid var(--orange);
  outline: none;
  padding: 0 1rem;
  color: black;
  background-color: rgba(255, 255, 255, 0.711);
}

.search-btn {
  font-size: 1.4rem;
  width: 12%;
  border-radius: 0 10px 10px 0;
  background-color: transparent;
  color: white;
  font-weight: 500;
  border: 3px solid var(--orange);
  border-left: none;
  transition: all 0.8s ease;
}

.search-btn:hover {
  background: #f48915;
}

.input-search::placeholder {
  font-size: 1rem;
  font-weight: 600;
  color: #4c4e53;
  opacity: 0.5;
}

.arrows {
  margin: 0 auto;
}

.arrow {
  width: 50px;
  height: 50px;
  margin: -10px 1rem;
  cursor: pointer;
  fill: aqua;
}

.arrow:hover {
  transition: 0.2s;
  transform: scale(1.25);
}

@media screen and (max-width: 600px) {
  .form {
    margin: 0;
    width: 100%;
  }

  .search-form {
    display: flex;
    width: 100%;
  }

  .input-search {
    width: 70%;
  }
  .search-btn {
    font-size: 1.2rem;
    width: 30%;
  }

  .swiper {
    max-height: 140px;
  }

  .category {
    padding: 1rem;
    min-height: 110px;
    justify-content: space-around;
  }

  .category > :nth-child(1) {
    width: 3rem;
    height: 3rem;
  }

  .category > :nth-child(2) {
    font-size: 1.2rem;
    justify-content: center;
    align-items: center;
  }
}
