.exercise-card {
  display: flex;
  min-height: 445px;
  width: 20rem;
  flex-direction: column;
  justify-content: space-between;
  border-bottom-right-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem;
  border-top: 8px solid var(--orange);
  transition: all 0.5s ease;
  background-color: gray;
  padding-bottom: 0.75rem;
}

.exercise-card:hover {
  transform: scale(1.05);
}

.tag {
  margin-left: 21px;
  color: white;
  font-size: 0.875rem;
  line-height: 1.25rem;
  padding: 0.25rem 0.75rem;
  border: 1px solid white;
  font-size: 14px;
  border-radius: 20px;
  text-transform: capitalize;
}

.text-bodyPart {
  margin-left: 21px;
  color: white;
  font-weight: 600;
  font-size: 1.5rem;
  line-height: 2rem;
  margin-bottom: 0.5rem;
  margin-top: 0;
  text-transform: capitalize;
}

.tag-container {
  display: flex;
  flex-direction: row;
}

@media screen and (max-width: 600px) {
  .exercise-card {
    width: 11rem;
    min-height: 260px;
    justify-content: space-between;
  }

  .text-bodyPart {
    margin-left: 10px;
    color: white;
    font-weight: 500;
    font-size: 1.2rem;
    line-height: 1.5rem;
    margin-bottom: 0;
    margin-top: 0;
    text-transform: capitalize;
  }

  .tag {
    margin-left: 5px;
    padding: 0.25rem 0.5rem;
    font-size: 14px;
  }

  .tag-container {
    justify-content: start;
  }

  .exercise-card:hover {
    transform: scale(1);
  }
}
